import 'moment/locale/cs';
import * as FullCalendar from 'fullcalendar';


/* Czech initialisation for the jQuery UI date picker plugin. */
/* Written by Tomas Muller (tomas@tomas-muller.net). */
FullCalendar.datepickerLocale('cs', 'cs', {
  closeText: "Zavt",
  prevText: "&#x3C;Dve",
  nextText: "Pozdji&#x3E;",
  currentText: "Nyn",
  monthNames: [ "leden","nor","bezen","duben","kvten","erven",
  "ervenec","srpen","z","jen","listopad","prosinec" ],
  monthNamesShort: [ "led","no","be","dub","kv","er",
  "vc","srp","z","j","lis","pro" ],
  dayNames: [ "nedle", "pondl", "ter", "steda", "tvrtek", "ptek", "sobota" ],
  dayNamesShort: [ "ne", "po", "t", "st", "t", "p", "so" ],
  dayNamesMin: [ "ne","po","t","st","t","p","so" ],
  weekHeader: "Td",
  dateFormat: "dd.mm.yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("cs", {
  buttonText: {
    month: "Msc",
    week: "Tden",
    day: "Den",
    list: "Agenda"
  },
  allDayText: "Cel den",
  eventLimitText: function(n) {
    return "+dal: " + n;
  },
  noEventsMessage: "dn akce k zobrazen"
});



