import 'moment/locale/lb';
import * as FullCalendar from 'fullcalendar';


/* Luxembourgish initialisation for the jQuery UI date picker plugin. */
/* Written by Michel Weimerskirch <michel@weimerskirch.net> */
FullCalendar.datepickerLocale('lb', 'lb', {
  closeText: "Ferdeg",
  prevText: "Zrck",
  nextText: "Weider",
  currentText: "Haut",
  monthNames: [ "Januar","Februar","Merz","Abrll","Mee","Juni",
  "Juli","August","September","Oktober","November","Dezember" ],
  monthNamesShort: [ "Jan", "Feb", "Me", "Abr", "Mee", "Jun",
  "Jul", "Aug", "Sep", "Okt", "Nov", "Dez" ],
  dayNames: [
    "Sonndeg",
    "Mindeg",
    "Dnschdeg",
    "Mttwoch",
    "Donneschdeg",
    "Freideg",
    "Samschdeg"
  ],
  dayNamesShort: [ "Son", "Mi", "Dn", "Mt", "Don", "Fre", "Sam" ],
  dayNamesMin: [ "So","M","D","M","Do","Fr","Sa" ],
  weekHeader: "W",
  dateFormat: "dd.mm.yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("lb", {
  buttonText: {
    month: "Mount",
    week: "Woch",
    day: "Dag",
    list: "Terminiwwersiicht"
  },
  allDayText: "Ganzen Dag",
  eventLimitText: "mi",
  noEventsMessage: "Nee Evenementer ze affichieren"
});



