import 'moment/locale/vi';
import * as FullCalendar from 'fullcalendar';


/* Vietnamese initialisation for the jQuery UI date picker plugin. */
/* Translated by Le Thanh Huy (lthanhhuy@cit.ctu.edu.vn). */
FullCalendar.datepickerLocale('vi', 'vi', {
  closeText: "ng",
  prevText: "&#x3C;Trc",
  nextText: "Tip&#x3E;",
  currentText: "Hm nay",
  monthNames: [ "Thng Mt", "Thng Hai", "Thng Ba", "Thng T", "Thng Nm", "Thng Su",
  "Thng By", "Thng Tm", "Thng Chn", "Thng Mi", "Thng Mi Mt", "Thng Mi Hai" ],
  monthNamesShort: [ "Thng 1", "Thng 2", "Thng 3", "Thng 4", "Thng 5", "Thng 6",
  "Thng 7", "Thng 8", "Thng 9", "Thng 10", "Thng 11", "Thng 12" ],
  dayNames: [ "Ch Nht", "Th Hai", "Th Ba", "Th T", "Th Nm", "Th Su", "Th By" ],
  dayNamesShort: [ "CN", "T2", "T3", "T4", "T5", "T6", "T7" ],
  dayNamesMin: [ "CN", "T2", "T3", "T4", "T5", "T6", "T7" ],
  weekHeader: "Tu",
  dateFormat: "dd/mm/yy",
  firstDay: 0,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("vi", {
  buttonText: {
    month: "Thng",
    week: "Tun",
    day: "Ngy",
    list: "Lch biu"
  },
  allDayText: "C ngy",
  eventLimitText: function(n) {
    return "+ thm " + n;
  },
  noEventsMessage: "Khng c s kin  hin th"
});



