import 'moment/locale/sr-cyrl';
import * as FullCalendar from 'fullcalendar';


/* Serbian i18n for the jQuery UI date picker plugin. */
/* Written by Dejan Dimi. */
FullCalendar.datepickerLocale('sr-cyrl', 'sr', {
  closeText: "",
  prevText: "&#x3C;",
  nextText: "&#x3E;",
  currentText: "",
  monthNames: [ "","","","","","",
  "","","","","","" ],
  monthNamesShort: [ "","","","","","",
  "","","","","","" ],
  dayNames: [ "","","","","","","" ],
  dayNamesShort: [ "","","","","","","" ],
  dayNamesMin: [ "","","","","","","" ],
  weekHeader: "",
  dateFormat: "dd.mm.yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("sr-cyrl", {
  buttonText: {
    prev: "",
    next: "",
    month: "",
    week: "",
    day: "",
    list: ""
  },
  allDayText: " ",
  eventLimitText: function(n) {
    return "+  " + n;
  },
  noEventsMessage: "   "
});



