import 'moment/locale/sq';
import * as FullCalendar from 'fullcalendar';


/* Albanian initialisation for the jQuery UI date picker plugin. */
/* Written by Flakron Bytyqi (flakron@gmail.com). */
FullCalendar.datepickerLocale('sq', 'sq', {
  closeText: "mbylle",
  prevText: "&#x3C;mbrapa",
  nextText: "Prpara&#x3E;",
  currentText: "sot",
  monthNames: [ "Janar","Shkurt","Mars","Prill","Maj","Qershor",
  "Korrik","Gusht","Shtator","Tetor","Nntor","Dhjetor" ],
  monthNamesShort: [ "Jan","Shk","Mar","Pri","Maj","Qer",
  "Kor","Gus","Sht","Tet","Nn","Dhj" ],
  dayNames: [ "E Diel","E Hn","E Mart","E Mrkur","E Enjte","E Premte","E Shtune" ],
  dayNamesShort: [ "Di","H","Ma","M","En","Pr","Sh" ],
  dayNamesMin: [ "Di","H","Ma","M","En","Pr","Sh" ],
  weekHeader: "Ja",
  dateFormat: "dd.mm.yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });

FullCalendar.locale("sq", {
  buttonText: {
    month: "Muaj",
    week: "Jav",
    day: "Dit",
    list: "List"
  },
  allDayHtml: "Gjith<br/>ditn",
  eventLimitText: function(n) {
    return "+m tepr " + n;
  },
  noEventsMessage: "Nuk ka evente pr t shfaqur"
});



