import 'moment/locale/hu';
import * as FullCalendar from 'fullcalendar';


/* Hungarian initialisation for the jQuery UI date picker plugin. */
FullCalendar.datepickerLocale('hu', 'hu', {
  closeText: "bezr",
  prevText: "vissza",
  nextText: "elre",
  currentText: "ma",
  monthNames: [ "Janur", "Februr", "Mrcius", "prilis", "Mjus", "Jnius",
  "Jlius", "Augusztus", "Szeptember", "Oktber", "November", "December" ],
  monthNamesShort: [ "Jan", "Feb", "Mr", "pr", "Mj", "Jn",
  "Jl", "Aug", "Szep", "Okt", "Nov", "Dec" ],
  dayNames: [ "Vasrnap", "Htf", "Kedd", "Szerda", "Cstrtk", "Pntek", "Szombat" ],
  dayNamesShort: [ "Vas", "Ht", "Ked", "Sze", "Cs", "Pn", "Szo" ],
  dayNamesMin: [ "V", "H", "K", "Sze", "Cs", "P", "Szo" ],
  weekHeader: "Ht",
  dateFormat: "yy.mm.dd.",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: true,
  yearSuffix: "" });


FullCalendar.locale("hu", {
  buttonText: {
    month: "Hnap",
    week: "Ht",
    day: "Nap",
    list: "Napl"
  },
  allDayText: "Egsz nap",
  eventLimitText: "tovbbi",
  noEventsMessage: "Nincs megjelenthet esemnyek"
});



