import 'moment/locale/gl';
import * as FullCalendar from 'fullcalendar';


/* Galician localization for 'UI date picker' jQuery extension. */
/* Translated by Jorge Barreiro <yortx.barry@gmail.com>. */
FullCalendar.datepickerLocale('gl', 'gl', {
  closeText: "Pechar",
  prevText: "&#x3C;Ant",
  nextText: "Seg&#x3E;",
  currentText: "Hoxe",
  monthNames: [ "Xaneiro","Febreiro","Marzo","Abril","Maio","Xuo",
  "Xullo","Agosto","Setembro","Outubro","Novembro","Decembro" ],
  monthNamesShort: [ "Xan","Feb","Mar","Abr","Mai","Xu",
  "Xul","Ago","Set","Out","Nov","Dec" ],
  dayNames: [ "Domingo","Luns","Martes","Mrcores","Xoves","Venres","Sbado" ],
  dayNamesShort: [ "Dom","Lun","Mar","Mr","Xov","Ven","Sb" ],
  dayNamesMin: [ "Do","Lu","Ma","M","Xo","Ve","S" ],
  weekHeader: "Sm",
  dateFormat: "dd/mm/yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("gl", {
  buttonText: {
    month: "Mes",
    week: "Semana",
    day: "Da",
    list: "Axenda"
  },
  allDayHtml: "Todo<br/>o da",
  eventLimitText: "mis",
  noEventsMessage: "Non hai eventos para amosar"
});



