import 'moment/locale/lv';
import * as FullCalendar from 'fullcalendar';


/* Latvian (UTF-8) initialisation for the jQuery UI date picker plugin. */
/* @author Arturas Paleicikas <arturas.paleicikas@metasite.net> */
FullCalendar.datepickerLocale('lv', 'lv', {
  closeText: "Aizvrt",
  prevText: "Iepr.",
  nextText: "Nk.",
  currentText: "odien",
  monthNames: [ "Janvris","Februris","Marts","Aprlis","Maijs","Jnijs",
  "Jlijs","Augusts","Septembris","Oktobris","Novembris","Decembris" ],
  monthNamesShort: [ "Jan","Feb","Mar","Apr","Mai","Jn",
  "Jl","Aug","Sep","Okt","Nov","Dec" ],
  dayNames: [
    "svtdiena",
    "pirmdiena",
    "otrdiena",
    "trediena",
    "ceturtdiena",
    "piektdiena",
    "sestdiena"
  ],
  dayNamesShort: [ "svt","prm","otr","tre","ctr","pkt","sst" ],
  dayNamesMin: [ "Sv","Pr","Ot","Tr","Ct","Pk","Ss" ],
  weekHeader: "Ned.",
  dateFormat: "dd.mm.yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("lv", {
  buttonText: {
    month: "Mnesis",
    week: "Neda",
    day: "Diena",
    list: "Dienas krtba"
  },
  allDayText: "Visu dienu",
  eventLimitText: function(n) {
    return "+vl " + n;
  },
  noEventsMessage: "Nav notikumu"
});



