import 'moment/locale/tr';
import * as FullCalendar from 'fullcalendar';


/* Turkish initialisation for the jQuery UI date picker plugin. */
/* Written by Izzet Emre Erkan (kara@karalamalar.net). */
FullCalendar.datepickerLocale('tr', 'tr', {
  closeText: "kapat",
  prevText: "&#x3C;geri",
  nextText: "ileri&#x3e",
  currentText: "bugn",
  monthNames: [ "Ocak","ubat","Mart","Nisan","Mays","Haziran",
  "Temmuz","Austos","Eyll","Ekim","Kasm","Aralk" ],
  monthNamesShort: [ "Oca","ub","Mar","Nis","May","Haz",
  "Tem","Au","Eyl","Eki","Kas","Ara" ],
  dayNames: [ "Pazar","Pazartesi","Sal","aramba","Perembe","Cuma","Cumartesi" ],
  dayNamesShort: [ "Pz","Pt","Sa","a","Pe","Cu","Ct" ],
  dayNamesMin: [ "Pz","Pt","Sa","a","Pe","Cu","Ct" ],
  weekHeader: "Hf",
  dateFormat: "dd.mm.yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("tr", {
  buttonText: {
    next: "ileri", // override JQUI's, which has a non-closing HTML entity in it
    month: "Ay",
    week: "Hafta",
    day: "Gn",
    list: "Ajanda"
  },
  allDayText: "Tm gn",
  eventLimitText: "daha fazla",
  noEventsMessage: "Herhangi bir etkinlik grntlemek iin"
});



