import 'moment/locale/et';
import * as FullCalendar from 'fullcalendar';


/* Estonian initialisation for the jQuery UI date picker plugin. */
/* Written by Mart Smermaa (mrts.pydev at gmail com). */
FullCalendar.datepickerLocale('et', 'et', {
  closeText: "Sulge",
  prevText: "Eelnev",
  nextText: "Jrgnev",
  currentText: "Tna",
  monthNames: [ "Jaanuar","Veebruar","Mrts","Aprill","Mai","Juuni",
  "Juuli","August","September","Oktoober","November","Detsember" ],
  monthNamesShort: [ "Jaan", "Veebr", "Mrts", "Apr", "Mai", "Juuni",
  "Juuli", "Aug", "Sept", "Okt", "Nov", "Dets" ],
  dayNames: [
    "Phapev",
    "Esmaspev",
    "Teisipev",
    "Kolmapev",
    "Neljapev",
    "Reede",
    "Laupev"
  ],
  dayNamesShort: [ "Phap", "Esmasp", "Teisip", "Kolmap", "Neljap", "Reede", "Laup" ],
  dayNamesMin: [ "P","E","T","K","N","R","L" ],
  weekHeader: "nd",
  dateFormat: "dd.mm.yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("et", {
  buttonText: {
    month: "Kuu",
    week: "Ndal",
    day: "Pev",
    list: "Pevakord"
  },
  allDayText: "Kogu pev",
  eventLimitText: function(n) {
    return "+ veel " + n;
  },
  noEventsMessage: "Kuvamiseks puuduvad sndmused"
});



