import 'moment/locale/fr';
import * as FullCalendar from 'fullcalendar';


/* French initialisation for the jQuery UI date picker plugin. */
/* Written by Keith Wood (kbwood{at}iinet.com.au),
        Stphane Nahmani (sholby@sholby.net),
        Stphane Raimbault <stephane.raimbault@gmail.com> */
FullCalendar.datepickerLocale('fr', 'fr', {
  closeText: "Fermer",
  prevText: "Prcdent",
  nextText: "Suivant",
  currentText: "Aujourd'hui",
  monthNames: [ "janvier", "fvrier", "mars", "avril", "mai", "juin",
    "juillet", "aot", "septembre", "octobre", "novembre", "dcembre" ],
  monthNamesShort: [ "janv.", "fvr.", "mars", "avr.", "mai", "juin",
    "juil.", "aot", "sept.", "oct.", "nov.", "dc." ],
  dayNames: [ "dimanche", "lundi", "mardi", "mercredi", "jeudi", "vendredi", "samedi" ],
  dayNamesShort: [ "dim.", "lun.", "mar.", "mer.", "jeu.", "ven.", "sam." ],
  dayNamesMin: [ "D","L","M","M","J","V","S" ],
  weekHeader: "Sem.",
  dateFormat: "dd/mm/yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("fr", {
  buttonText: {
    year: "Anne",
    month: "Mois",
    week: "Semaine",
    day: "Jour",
    list: "Mon planning"
  },
  allDayHtml: "Toute la<br/>journe",
  eventLimitText: "en plus",
  noEventsMessage: "Aucun vnement  afficher"
});



