import 'moment/locale/lt';
import * as FullCalendar from 'fullcalendar';


/* Lithuanian (UTF-8) initialisation for the jQuery UI date picker plugin. */
/* @author Arturas Paleicikas <arturas@avalon.lt> */
FullCalendar.datepickerLocale('lt', 'lt', {
  closeText: "Udaryti",
  prevText: "&#x3C;Atgal",
  nextText: "Pirmyn&#x3E;",
  currentText: "iandien",
  monthNames: [ "Sausis","Vasaris","Kovas","Balandis","Gegu","Birelis",
  "Liepa","Rugpjtis","Rugsjis","Spalis","Lapkritis","Gruodis" ],
  monthNamesShort: [ "Sau","Vas","Kov","Bal","Geg","Bir",
  "Lie","Rugp","Rugs","Spa","Lap","Gru" ],
  dayNames: [
    "sekmadienis",
    "pirmadienis",
    "antradienis",
    "treiadienis",
    "ketvirtadienis",
    "penktadienis",
    "etadienis"
  ],
  dayNamesShort: [ "sek","pir","ant","tre","ket","pen","e" ],
  dayNamesMin: [ "Se","Pr","An","Tr","Ke","Pe","e" ],
  weekHeader: "SAV",
  dateFormat: "yy-mm-dd",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: true,
  yearSuffix: "" });


FullCalendar.locale("lt", {
  buttonText: {
    month: "Mnuo",
    week: "Savait",
    day: "Diena",
    list: "Darbotvark"
  },
  allDayText: "Vis dien",
  eventLimitText: "daugiau",
  noEventsMessage: "Nra vyki rodyti"
});



