import 'moment/locale/is';
import * as FullCalendar from 'fullcalendar';


/* Icelandic initialisation for the jQuery UI date picker plugin. */
/* Written by Haukur H. Thorsson (haukur@eskill.is). */
FullCalendar.datepickerLocale('is', 'is', {
  closeText: "Loka",
  prevText: "&#x3C; Fyrri",
  nextText: "Nsti &#x3E;",
  currentText: " dag",
  monthNames: [ "Janar","Febrar","Mars","Aprl","Ma","Jn",
  "Jl","gst","September","Oktber","Nvember","Desember" ],
  monthNamesShort: [ "Jan","Feb","Mar","Apr","Ma","Jn",
  "Jl","g","Sep","Okt","Nv","Des" ],
  dayNames: [
    "Sunnudagur",
    "Mnudagur",
    "rijudagur",
    "Mivikudagur",
    "Fimmtudagur",
    "Fstudagur",
    "Laugardagur"
  ],
  dayNamesShort: [ "Sun","Mn","ri","Mi","Fim","Fs","Lau" ],
  dayNamesMin: [ "Su","M","r","Mi","Fi","F","La" ],
  weekHeader: "Vika",
  dateFormat: "dd.mm.yy",
  firstDay: 0,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("is", {
  buttonText: {
    month: "Mnuur",
    week: "Vika",
    day: "Dagur",
    list: "Dagskr"
  },
  allDayHtml: "Allan<br/>daginn",
  eventLimitText: "meira",
  noEventsMessage: "Engir viburir til a sna"
});



