import 'moment/locale/pl';
import * as FullCalendar from 'fullcalendar';


/* Polish initialisation for the jQuery UI date picker plugin. */
/* Written by Jacek Wysocki (jacek.wysocki@gmail.com). */
FullCalendar.datepickerLocale('pl', 'pl', {
  closeText: "Zamknij",
  prevText: "&#x3C;Poprzedni",
  nextText: "Nastpny&#x3E;",
  currentText: "Dzi",
  monthNames: [ "Stycze","Luty","Marzec","Kwiecie","Maj","Czerwiec",
  "Lipiec","Sierpie","Wrzesie","Padziernik","Listopad","Grudzie" ],
  monthNamesShort: [ "Sty","Lu","Mar","Kw","Maj","Cze",
  "Lip","Sie","Wrz","Pa","Lis","Gru" ],
  dayNames: [ "Niedziela","Poniedziaek","Wtorek","roda","Czwartek","Pitek","Sobota" ],
  dayNamesShort: [ "Nie","Pn","Wt","r","Czw","Pt","So" ],
  dayNamesMin: [ "N","Pn","Wt","r","Cz","Pt","So" ],
  weekHeader: "Tydz",
  dateFormat: "dd.mm.yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("pl", {
  buttonText: {
    month: "Miesic",
    week: "Tydzie",
    day: "Dzie",
    list: "Plan dnia"
  },
  allDayText: "Cay dzie",
  eventLimitText: "wicej",
  noEventsMessage: "Brak wydarze do wywietlenia"
});



