import 'moment/locale/sv';
import * as FullCalendar from 'fullcalendar';


/* Swedish initialisation for the jQuery UI date picker plugin. */
/* Written by Anders Ekdahl ( anders@nomadiz.se). */
FullCalendar.datepickerLocale('sv', 'sv', {
  closeText: "Stng",
  prevText: "&#xAB;Frra",
  nextText: "Nsta&#xBB;",
  currentText: "Idag",
  monthNames: [ "Januari","Februari","Mars","April","Maj","Juni",
  "Juli","Augusti","September","Oktober","November","December" ],
  monthNamesShort: [ "Jan","Feb","Mar","Apr","Maj","Jun",
  "Jul","Aug","Sep","Okt","Nov","Dec" ],
  dayNamesShort: [ "Sn","Mn","Tis","Ons","Tor","Fre","Lr" ],
  dayNames: [ "Sndag","Mndag","Tisdag","Onsdag","Torsdag","Fredag","Lrdag" ],
  dayNamesMin: [ "S","M","Ti","On","To","Fr","L" ],
  weekHeader: "v. ",
  dateFormat: "yy-mm-dd",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("sv", {
  buttonText: {
    month: "Mnad",
    week: "Vecka",
    day: "Dag",
    list: "Program"
  },
  allDayText: "Heldag",
  eventLimitText: "till",
  noEventsMessage: "Inga hndelser att visa"
});



