import 'moment/locale/ro';
import * as FullCalendar from 'fullcalendar';


/* Romanian initialisation for the jQuery UI date picker plugin.
 *
 * Written by Edmond L. (ll_edmond@walla.com)
 * and Ionut G. Stan (ionut.g.stan@gmail.com)
 */
FullCalendar.datepickerLocale('ro', 'ro', {
  closeText: "nchide",
  prevText: "&#xAB; Luna precedent",
  nextText: "Luna urmtoare &#xBB;",
  currentText: "Azi",
  monthNames: [ "Ianuarie","Februarie","Martie","Aprilie","Mai","Iunie",
  "Iulie","August","Septembrie","Octombrie","Noiembrie","Decembrie" ],
  monthNamesShort: [ "Ian", "Feb", "Mar", "Apr", "Mai", "Iun",
  "Iul", "Aug", "Sep", "Oct", "Nov", "Dec" ],
  dayNames: [ "Duminic", "Luni", "Mari", "Miercuri", "Joi", "Vineri", "Smbt" ],
  dayNamesShort: [ "Dum", "Lun", "Mar", "Mie", "Joi", "Vin", "Sm" ],
  dayNamesMin: [ "Du","Lu","Ma","Mi","Jo","Vi","S" ],
  weekHeader: "Spt",
  dateFormat: "dd.mm.yy",
  firstDay: 1,
  isRTL: false,
  showMonthAfterYear: false,
  yearSuffix: "" });


FullCalendar.locale("ro", {
  buttonText: {
    prev: "precedent", // override JQUI's translations, which contains the word "month"
    next: "urmtoare",  // "
    month: "Lun",
    week: "Sptmn",
    day: "Zi",
    list: "Agend"
  },
  allDayText: "Toat ziua",
  eventLimitText: function(n) {
    return "+alte " + n;
  },
  noEventsMessage: "Nu exist evenimente de afiat"
});



